// @ts-nocheck

import { Ref } from 'vue-demi';
import { ref, toRefs, watch, computed } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { MESSAGES_QUERY } from '@/graphql';
import { mergeProperty } from '@/common';

interface Variables {
  chat_id: Ref<number> | number;
  page?: Ref<number> | number;
}

export default function useChatMessagesQuery(variables: Variables, options?: UseQueryOptions) {
  const { result, loading, error, fetchMore, refetch } = useQuery(MESSAGES_QUERY, variables, options);

  const messages = useResult(result, [], (data: any) => {
    // 按时间早->晚排序
    return Array.isArray(data?.messages?.data) ? [...data?.messages?.data].reverse() : [];
  });
  const paginatorInfo = useResult(result, {}, (data: any) => data?.messages?.paginatorInfo);

  const currentPage = ref(paginatorInfo?.currentPage);
  const hasMorePages = ref(paginatorInfo?.hasMorePages);
  watch(paginatorInfo, (value, prevValue) => {
    currentPage.value = value?.currentPage;
    hasMorePages.value = value?.hasMorePages;
  });
  const loadingMore = ref(false);
  const disabled = computed(() => loading.value || loadingMore.value || !hasMorePages.value);

  function loadMore() {
    loadingMore.value = true;
    fetchMore({
      variables: {
        page: currentPage.value + 1
      },
      updateQuery: (prev: any, { fetchMoreResult }) => {
        loadingMore.value = false;
        if (!fetchMoreResult) {
          return prev;
        }
        return mergeProperty(prev, fetchMoreResult);
      }
    });
  }

  return {
    error,
    refetch,
    messages,
    disabled,
    loading,
    loadMore,
    hasMorePages,
    loadingMore,
    currentPage
  };
}
