import { gql } from '@apollo/client/core';

export const CHAT_FRAGMENT = gql`
  fragment chatFragment on Chat {
    id
    subject
    icon
    unreads
    time_ago
    updated_at
    lastMessage {
      id
      message
    }
    user {
      id
      name
      avatar
    }
    users {
      id
      name
      avatar
    }
    withUser {
      id
      name
      avatar
      gender
      age
    }
    type
  }
`;

// 聊天列表
export const CHATS_QUERY = gql`
  ${CHAT_FRAGMENT}

  query chatsQuery($user_id: Int!, $page: Int) {
    chats(user_id: $user_id, page: $page) {
      paginatorInfo {
        currentPage
        hasMorePages
      }
      data {
        ...chatFragment
      }
    }
  }
`;

// 查询单个群聊信息
export const CHAT_QUERY = gql`
  ${CHAT_FRAGMENT}

  query chatQuery($chat_id: Int!) {
    chat(chat_id: $chat_id) {
      ...chatFragment
    }
  }
`;

export const MESSAGES_QUERY = gql`
  query messagesQuery($chat_id: Int!, $page: Int) {
    messages(chat_id: $chat_id, page: $page) {
      paginatorInfo {
        currentPage
        hasMorePages
      }
      data {
        id
        message
        type
        body {
          text
          url
        }
        messageable_id
        messageable_type
        messageable {
          ... on Article {
            id
            type
            title
            description
            images {
              id
              url
            }
          }
        }
        time_ago
        created_at
        user {
          id
          name
          avatar
        }
      }
    }
  }
`;

// 创建聊天
export const CREATE_CHAT_MUTATION = gql`
  ${CHAT_FRAGMENT}

  mutation createChatMutation($uids: [Int!], $subject: String, $status: ChatStatus, $type: ChatType) {
    createChat(uids: $uids, subject: $subject, status: $status, type: $type) {
      ...chatFragment
    }
  }
`;

// 删除功能只从列表删除聊天对话框
// 重新创建对话框时，可以查询出之前的历史记录
export const DELETE_CHAT_MUTATION = gql`
  mutation deleteChatMutation($chat_id: Int!) {
    deleteChat(chat_id: $chat_id) {
      id
    }
  }
`;

// 修改群聊信息
export const UPDATE_CHAT_MUTATION = gql`
  mutation updateChatMutation($chat_id: Int!, $subject: String, $icon: String, $status: ChatStatus) {
    updateChat(chat_id: $chat_id, subject: $subject, icon: $icon, status: $status) {
      id
      subject
      icon
      status
    }
  }
`;

// 发送消息
export const SEND_MESSAGE_MUTATION = gql`
  mutation sendMessageMutation(
    $user_id: Int!
    $chat_id: Int!
    $message: String!
    $url: String
    $messageable_id: Int
    $messageable_type: MessageableType
  ) {
    sendMessage(
      user_id: $user_id
      chat_id: $chat_id
      text: $message
      url: $url
      messageable_id: $messageable_id
      messageable_type: $messageable_type
    ) {
      id
      message
      body {
        text
        url
      }
      time_ago
      created_at
      user {
        id
        name
        avatar
      }
      messageable_id
      messageable_type
      messageable {
        ... on Article {
          id
          title
          type
          description
          images {
            id
            url
          }
        }
      }
    }
  }
`;
